"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { getJSON, postJSON } from "@/lib/clientApi";
import type { Character } from "@/lib/models";

const spriteOptions = ["default", "guerreiro", "arqueira", "mago"];

type AuthResult = { id: string; email: string };

type CharacterListResponse = {
  characters: Character[];
};

const pagePreviews = [
  { id: "login", label: "/login — autenticação dedicada", path: "/login" },
  { id: "character", label: "/character — gerenciamento de personagens", path: "/character" },
  { id: "city", label: "/city — Phaser + inventário + online", path: "/city" },
  { id: "house", label: "/house — mobília", path: "/house" },
  { id: "farm", label: "/farm — plantio", path: "/farm" },
  { id: "forest", label: "/forest — combate", path: "/forest" },
  { id: "shops", label: "/shops — lojas", path: "/shops" },
  { id: "chat", label: "/chat — chat global", path: "/chat" },
  { id: "editor", label: "/editor — Admin/Editor de mapas", path: "/editor" }
] as const;

type PreviewId = (typeof pagePreviews)[number]["id"];
type ModalView = "register" | "login" | "character";

export default function HomePage() {
  const router = useRouter();
  const [registerData, setRegisterData] = useState({ email: "", password: "" });
  const [loginData, setLoginData] = useState({ email: "", password: "" });
  const [authFeedback, setAuthFeedback] = useState<string | null>(null);
  const [loadingAuth, setLoadingAuth] = useState(false);
  const [user, setUser] = useState<AuthResult | null>(null);
  const [characters, setCharacters] = useState<Character[]>([]);
  const [selectedCharacter, setSelectedCharacter] = useState("");
  const [characterForm, setCharacterForm] = useState({ name: "", sprite: spriteOptions[0] });
  const [characterFeedback, setCharacterFeedback] = useState<string | null>(null);
  const [refreshingCharacters, setRefreshingCharacters] = useState(false);
  const [selectedPreview, setSelectedPreview] = useState<PreviewId>(pagePreviews[0].id);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalView, setModalView] = useState<ModalView>("register");
  const activePreview =
    pagePreviews.find((preview) => preview.id === selectedPreview) ?? pagePreviews[0];

  const characterLimitReached = characters.length >= 4;

  useEffect(() => {
    if (!user?.id) {
      setCharacters([]);
      setSelectedCharacter("");
      return;
    }
    (async () => {
      await refreshCharacters(user.id);
    })();
  }, [user?.id]);

  useEffect(() => {
    if (user?.id) {
      openModal("character");
    }
  }, [user?.id]);

  function openModal(view: ModalView) {
    setModalView(view);
    setIsModalOpen(true);
  }

  function closeModal() {
    setIsModalOpen(false);
  }

  async function handleRegister(event: React.FormEvent<HTMLFormElement>) {
    event.preventDefault();
    setLoadingAuth(true);
    setAuthFeedback(null);
    try {
      const result = await postJSON<AuthResult>("/api/auth/register", registerData);
      setUser(result);
      setAuthFeedback(`Usuário registrado: ${result.email}`);
      setModalView("character");
      setIsModalOpen(true);
    } catch (err) {
      setAuthFeedback(getMessage(err));
    } finally {
      setLoadingAuth(false);
    }
  }

  async function handleLogin(event: React.FormEvent<HTMLFormElement>) {
    event.preventDefault();
    setLoadingAuth(true);
    setAuthFeedback(null);
    try {
      const result = await postJSON<AuthResult>("/api/auth/login", loginData);
      setUser(result);
      setAuthFeedback(`Login efetuado para ${result.email}`);
      setModalView("character");
      setIsModalOpen(true);
    } catch (err) {
      setAuthFeedback(getMessage(err));
    } finally {
      setLoadingAuth(false);
    }
  }

  async function handleCreateCharacter(event: React.FormEvent<HTMLFormElement>) {
    event.preventDefault();
    if (!user?.id) {
      setCharacterFeedback("Faça login antes de criar personagens");
      return;
    }
    setCharacterFeedback(null);
    try {
      await postJSON<Character>("/api/character/create", {
        ownerId: user.id,
        name: characterForm.name,
        sprite: characterForm.sprite
      });
      setCharacterForm({ name: "", sprite: spriteOptions[0] });
      await refreshCharacters(user.id);
      setCharacterFeedback("Personagem criado com sucesso.");
    } catch (err) {
      setCharacterFeedback(getMessage(err));
    }
  }

  async function refreshCharacters(ownerId: string) {
    setRefreshingCharacters(true);
    setCharacterFeedback(null);
    try {
      const response = await getJSON<CharacterListResponse>(`/api/character/get?ownerId=${ownerId}`);
      setCharacters(response.characters);
      if (response.characters.length === 0) {
        setSelectedCharacter("");
      } else {
        setSelectedCharacter((current) => {
          if (current && response.characters.find((char) => char._id === current)) {
            return current;
          }
          return response.characters[0]._id ?? "";
        });
      }
    } catch (err) {
      setCharacters([]);
      setSelectedCharacter("");
      setCharacterFeedback(getMessage(err));
    } finally {
      setRefreshingCharacters(false);
    }
  }

  function enterCity() {
    if (!user?.id || !selectedCharacter) {
      setCharacterFeedback("Selecione um personagem para continuar.");
      return;
    }
    router.push(`/city?ownerId=${user.id}&characterId=${selectedCharacter}`);
    setIsModalOpen(false);
  }

  const highlightedCharacters = characters.slice(0, 3);

  return (
    <>
      <section className="hero">
        <div className="hero-content">
          <p className="hero-badge">MVP — Mundo 2D Spinazzi</p>
          <h1>Construa sua vila, entre na cidade e mantenha tudo no servidor.</h1>
          <p>
            Cadastre-se, crie até quatro personagens e entre direto no mapa da cidade (Phaser +
            inventário + chat). Todo o fluxo respeita o README oficial e usa as rotas já implementadas.
          </p>
          <div className="hero-actions">
            <button className="gui-button" type="button" onClick={() => openModal("register")}>
              Começar agora
            </button>
            <button className="gui-button secondary" type="button" onClick={() => openModal("login")}>
              Já tenho conta
            </button>
            <button
              className="gui-link"
              type="button"
              onClick={() => openModal("character")}
              disabled={!user?.id}
            >
              Gerenciar personagens
            </button>
          </div>
          {authFeedback && <p className="status-message">{authFeedback}</p>}
          {user?.id && (
            <p className="status-message subtle">
              Logado como <strong>{user.email}</strong> — ownerId <code>{user.id}</code>
            </p>
          )}
        </div>
        <div className="hero-art">
          <p>Primeiro mapa desbloqueado</p>
          <h3>Cidade — Phaser 2D</h3>
          <ul>
            <li>Personagens criados: {characters.length}</li>
            <li>Limite por conta: 4</li>
            <li>Porta dev: 3001 fixa</li>
          </ul>
          <div className="hero-party">
            {highlightedCharacters.length > 0 ? (
              highlightedCharacters.map((character) => (
                <button
                  key={character._id ?? character.name}
                  type="button"
                  className={`hero-party-card${selectedCharacter === (character._id ?? "") ? " active" : ""}`}
                  onClick={() => {
                    const characterId = character._id ?? "";
                    if (characterId) {
                      setSelectedCharacter(characterId);
                      openModal("character");
                    }
                  }}
                  disabled={!character._id}
                >
                  <span>{character.name}</span>
                  <small>Sprite: {character.sprite}</small>
                </button>
              ))
            ) : (
              <p>Nenhum personagem carregado ainda.</p>
            )}
          </div>
        </div>
      </section>

      <section className="card gui-panel">
        <div className="section-header">
          <div>
            <h3>Seleção rápida de personagens</h3>
            <p>Faça login, crie até 4 personagens e defina qual entrará na cidade.</p>
          </div>
          <button className="button" type="button" onClick={() => openModal("character")}>
            Abrir gerenciador
          </button>
        </div>
        <div className="character-grid">
          {user?.id ? (
            characters.length > 0 ? (
              characters.map((character) => {
                const characterId = character._id ?? "";
                const isSelected = selectedCharacter === characterId;
                return (
                  <div className={`character-card${isSelected ? " selected" : ""}`} key={characterId || character.name}>
                    <div>
                      <h4>{character.name}</h4>
                      <p>Sprite: {character.sprite}</p>
                      <p>Nível {character.stats.level}</p>
                    </div>
                    <button
                      className="gui-link"
                      type="button"
                      onClick={() => {
                        setSelectedCharacter(characterId);
                        openModal("character");
                      }}
                      disabled={!characterId}
                    >
                      {isSelected ? "Selecionado" : "Definir ativo"}
                    </button>
                  </div>
                );
              })
            ) : (
              <p>Nenhum personagem criado para este ownerId.</p>
            )
          ) : (
            <p>Faça login para exibir a lista de personagens.</p>
          )}
        </div>
      </section>

      <section className="card gui-panel">
        <h3>Visualizar todas as rotas sem sair de /</h3>
        <p>
          O hub abaixo usa iframes locais para garantir que o usuário não precise alterar a barra de
          endereço manualmente.
        </p>
        <label className="sr-only" htmlFor="preview-select">
          Selecionar página
        </label>
        <select
          id="preview-select"
          value={selectedPreview}
          onChange={(event) => setSelectedPreview(event.target.value as PreviewId)}
        >
          {pagePreviews.map((preview) => (
            <option key={preview.id} value={preview.id}>
              {preview.label}
            </option>
          ))}
        </select>
        <div className="preview-frame">
          {activePreview && (
            <iframe
              key={selectedPreview}
              title={`preview-${selectedPreview}`}
              src={`${activePreview.path}?embed=1`}
              style={{ width: "100%", height: "600px", border: "none", background: "#000" }}
            />
          )}
        </div>
      </section>

      {isModalOpen && (
        <div className="modal-overlay" role="dialog" aria-modal="true" onClick={closeModal}>
          <div className="modal-card" onClick={(event) => event.stopPropagation()}>
            <div className="modal-tabs">
              <button
                type="button"
                className={modalView === "register" ? "active" : ""}
                onClick={() => setModalView("register")}
              >
                Registrar
              </button>
              <button
                type="button"
                className={modalView === "login" ? "active" : ""}
                onClick={() => setModalView("login")}
              >
                Login
              </button>
              <button
                type="button"
                className={modalView === "character" ? "active" : ""}
                onClick={() => setModalView("character")}
              >
                Personagens
              </button>
              <button type="button" className="modal-close" onClick={closeModal}>
                ×
              </button>
            </div>
            <div className="modal-content">{renderModalContent()}</div>
          </div>
        </div>
      )}
    </>
  );

  function renderModalContent() {
    if (modalView === "register") {
      return (
        <div className="modal-section">
          <h3>Registrar conta</h3>
          <p>Insira e-mail e senha (mínimo 6 caracteres) para gerar um novo ownerId.</p>
          <form onSubmit={handleRegister}>
            <label htmlFor="register-email">E-mail</label>
            <input
              id="register-email"
              type="email"
              required
              value={registerData.email}
              onChange={(event) =>
                setRegisterData((prev) => ({ ...prev, email: event.target.value }))
              }
            />
            <label htmlFor="register-password">Senha</label>
            <input
              id="register-password"
              type="password"
              required
              minLength={6}
              value={registerData.password}
              onChange={(event) =>
                setRegisterData((prev) => ({ ...prev, password: event.target.value }))
              }
            />
            <button className="button" disabled={loadingAuth}>
              Registrar
            </button>
          </form>
        </div>
      );
    }

    if (modalView === "login") {
      return (
        <div className="modal-section">
          <h3>Login</h3>
          <p>Use uma conta existente para carregar os personagens já criados.</p>
          <form onSubmit={handleLogin}>
            <label htmlFor="login-email">E-mail</label>
            <input
              id="login-email"
              type="email"
              required
              value={loginData.email}
              onChange={(event) => setLoginData((prev) => ({ ...prev, email: event.target.value }))}
            />
            <label htmlFor="login-password">Senha</label>
            <input
              id="login-password"
              type="password"
              required
              value={loginData.password}
              onChange={(event) =>
                setLoginData((prev) => ({ ...prev, password: event.target.value }))
              }
            />
            <button className="button" disabled={loadingAuth}>
              Entrar
            </button>
          </form>
        </div>
      );
    }

    return (
      <div className="modal-section">
        <h3>Gerenciar personagens</h3>
        {user ? (
          <>
            <p>
              Owner ID: <code>{user.id}</code>. Escolha um personagem e confirme para seguir à
              cidade.
            </p>
            {refreshingCharacters && <p>Carregando personagens…</p>}
            {characterFeedback && <p>{characterFeedback}</p>}
            <ul className="list modal-list">
              {characters.map((character) => {
                const characterId = character._id ?? "";
                return (
                  <li key={character._id ?? character.name}>
                    <label className="character-radio">
                      <input
                        type="radio"
                        name="selectedCharacter"
                        value={characterId}
                        checked={selectedCharacter === characterId}
                        onChange={() => setSelectedCharacter(characterId)}
                        disabled={!characterId}
                      />
                      <span>
                        {character.name} — sprite {character.sprite} — nível {character.stats.level}
                      </span>
                    </label>
                  </li>
                );
              })}
              {characters.length === 0 && <li>Nenhum personagem criado.</li>}
            </ul>

            <form onSubmit={handleCreateCharacter}>
              <h4>Criar novo personagem</h4>
              <label htmlFor="character-name">Nome</label>
              <input
                id="character-name"
                required
                minLength={3}
                value={characterForm.name}
                onChange={(event) =>
                  setCharacterForm((prev) => ({ ...prev, name: event.target.value }))
                }
                disabled={characterLimitReached}
              />
              <label htmlFor="character-sprite">Sprite</label>
              <select
                id="character-sprite"
                value={characterForm.sprite}
                onChange={(event) =>
                  setCharacterForm((prev) => ({ ...prev, sprite: event.target.value }))
                }
                disabled={characterLimitReached}
              >
                {spriteOptions.map((option) => (
                  <option key={option}>{option}</option>
                ))}
              </select>
              <button className="button" disabled={characterLimitReached}>
                {characterLimitReached ? "Limite de 4 alcançado" : "Criar Personagem"}
              </button>
            </form>

            <button
              className="button"
              style={{ marginTop: 24 }}
              type="button"
              onClick={enterCity}
              disabled={!selectedCharacter}
            >
              Entrar no primeiro mapa (/city)
            </button>
          </>
        ) : (
          <div>
            <p>Faça login antes de criar ou selecionar personagens.</p>
            <button className="button" type="button" onClick={() => setModalView("login")}>
              Ir para o login
            </button>
          </div>
        )}
      </div>
    );
  }
}

function getMessage(err: unknown) {
  return err instanceof Error ? err.message : "Ação não concluída";
}
